.NavBar {
  display: -ms-flexbox;
  display: flex;
  background-color: #25283D;
  -ms-flex-direction: row;
      flex-direction: row;
  padding-left: 2%;
  padding-right: 5%;
  color: #EFD9CE;
}

img {
  border-radius: 50%;
  width: 10%;
  height: 10%;
  -ms-flex-item-align: center;
      align-self: center;
}

h1 {
  padding-left: 3%;
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 24px;
  -ms-flex-item-align: center;
      align-self: center;
}

nav {
  -ms-flex: 2 1;
      flex: 2 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-left: 15%;
  text-align: right;
}

nav a {
  color: #EFD9CE;
  font-size: 18px;
  text-decoration: none;
  -ms-flex-item-align: center;
      align-self: center;
}

nav a:hover {
  color: #CEA2AC;
}

@media only screen and (max-width: 680px) {
  h1 {
    font-size: 20px;
  }
  nav a {
    font-size: 14px;
  }
}



